import styles from "../Projet/projet.module.css";

export default function Projet(props) {
  return (
    <div className={styles.container}>
      <div className={styles.block1}>
        <div className={styles.image_box}>
          <img src={props.image1} alt="s" />
        </div>
        <div className={styles.text}>
          <h2>{props.titre}</h2>
          <p>{props.explication} </p>
          <a href={props.lien} download={props.nomFichierTelechargee}>
            {props.txtButton}
          </a>
        </div>
      </div>
      <div className={styles.block2}>
        <div className={styles.sous_block}>
          <div>
            <img src={props.image2} alt="s" />
          </div>
          <div className={styles.explication}>
            <h3>{props.action1}</h3>
            <p>{props.explication1}</p>
          </div>
        </div>
        <div className={styles.sous_block}>
          <div>
            <img src={props.image3} alt="s" />
          </div>
          <div className={styles.explication}>
            <h3>{props.action2}</h3>
            <p>{props.explication2}</p>
          </div>
        </div>
        <div className={styles.sous_block}>
          <div>
            <img src={props.image4} alt="s" />
          </div>
          <div className={styles.explication}>
            <h3>{props.action3}</h3>
            <p>{props.explication3}</p>
          </div>
        </div>
      </div>
    </div>
  );
}
